<template>
<div id="background">
  <div id="fade"></div>
  <div id="image"></div>
</div>
</template>

<script>
export default {
  name: 'Background',
  data () {
    return {
      msg: 'what'
    }
  }
}
</script>

<style>
#background {
  position: fixed;
  bottom: 0;
  height: 5rem;
  width: 100%;
  z-index: -1;
}

#fade {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(to bottom, rgb(255, 255, 255, 1), rgba(0, 0, 0, 0));
}

#image {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  background-image: url('../assets/images/lights.png');
  background-repeat: no-repeat;
  background-size: cover;
}
</style>



