<template>
<div id="app">
  <NavMenu nav-open></NavMenu> 
  <Background />
  <div id="main">
    <TheHome />
    <TheStory />
    <TheWedding />
    <TheRsvp />
  </div>
</div>
</template>

<script>
import NavMenu from './components/NavMenu'
import Background from './components/Background'
import TheHome from './components/pages/TheHome'
import TheStory from './components/pages/TheStory'
import TheWedding from './components/pages/TheWedding'
import TheRsvp from './components/pages/TheRsvp'

export default {
  name: 'app',
  components: {
    NavMenu,
    Background,
    TheHome,
    TheStory,
    TheWedding,
    TheRsvp
  },
  data () {
    return {
      navOpen: false
    }
  }
}
</script>

<style>
:root {
  --blue-color: #10253F;
  --blush-color: #FFCCCC;
  --melon-color: #F7B2B7;
  --white-color: #FCFFFC;
  --teal-color: #5bc0be;
  --gold-color: #b8834e;
}

@font-face {
  font-family: 'Salmela';
  src: url('assets/fonts/salmela.otf') format('opentype');
  font-weight: normal;
  font-style: normal;
}

body, html {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  background-color: var(--white-color);
  color: var(--blue-color);
}

#app {
  font-family: 'Work Sans', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#main {
  position: absolute;
  height: 100%;
  top: 0;
}

section {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
  height: 100vh;
  text-align: center;
  background: transparent;
}

a {
  color: var(--gold-color);
}
a:clicked {
  color: var(--gold-color);
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Salmela', serif;
}
h2 {
  font-size: 2em;
  color: var(--gold-color);
}
h3 {
  color: var(--gold-color);
}

@media (min-width: 500px) {
  #main {
    width: 100%;
  }
}
</style>



