<template>
<section id="rsvp">
  <div id="title">
    <h2>RSVP</h2>
  </div>
  <div id="form-container">
    <div class="form-field">
      <label>Your full name</label>
      <input type="name"
        name="fullName"
        v-model.trim="fullName"
        placeholder="Full Name"
        v-validate="'required|alpha_spaces'"></input>
    </div>
    <div class="form-field">
      <label>Number of people?</label>
      <select name="people"
        v-model.number="people"
        v-validate="'required'">
        <option disabled value="">Please select one</option>
        <option value="1">1</option>
        <option value="2">2</option>
      </select>
    </div>
    <div class="form-field">
      <button type="submit">Submit</button>
    </div>
  </div><!-- #form-container -->
  <div id="display">
    <span v-if="fullName">{{ displayRsvp }}</span>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheRsvp',
  data () {
    return {
      msg: 'What',
      fullName: '',
      people: 1
    }
  },
  computed: {
    displayRsvp () {
      if (this.$validator.errors.has('fullName')) {
        return 'Please enter a valid name.'
      } else if (this.$validator.errors.has('people')) {
        return 'Will you bring a guest?'
      } else {
        if (this.people === 2) {
          return this.fullName + ' will be there with 1 guest.'
        } else {
          return this.fullName + ' will be there.'
        }
      }
    }
  }
}
</script>

<style scoped>
#form-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-content: center;
}

.form-field {
  text-align: center;
}

.form-field label {
  display: block;
}

#display {
  flex: 1;
}
</style>



