<script>
export default {
  name: 'TheWedding',
  data () {
    return {
      msg: 'What'
    }
  }
}
</script>

<style scoped>
#info-container {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-content: center;
}

.info {
  flex: 1;
}

.info h3 {
  font-family: 'Merriweather';
}

#map {
  flex: 1;
}
</style>



