<template>
<nav>
  <div id="nav-container" @click="navOpen = !navOpen">
    <div id="nav-icon" v-bind:class="{open: navOpen}">
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
  <div id="nav-menu" v-bind:class="{open: navOpen}">
    <div class="nav-link">
      <a @click="navOpen = !navOpen" href="#home">Home</a>
    </div>
    <div class="nav-link">
      <a @click="navOpen = !navOpen" href="#story">Their Story</a>
    </div>
    <div class="nav-link">
      <a @click="navOpen = !navOpen" href="#wedding">Wedding</a>
    </div>
    <div class="nav-link">
      <a @click="navOpen = !navOpen" href="#rsvp">RSVP</a>
    </div>
  </div>
</nav>
</template>

<script>
export default {
  name: 'nav-menu',
  props: ['initialOpen'],
  data () {
    return {
      navOpen: this.initialOpen
    }
  }
}
</script>

<style scoped>
#nav-container {
  position: fixed;
  right: 0;
  height: 2em;
  background-color: transparent;
  padding: .5em;
  z-index: 999;
  cursor: pointer;
}

#nav-icon {
  width: 2em;
  height: 100%;
  transform: rotate(0deg);
  transition: .5s ease-in-out;
}

#nav-icon span {
  display: block;
  position: absolute;
  height: .25em;
  width: 100%;
  background-color: var(--gold-color);
  border-radius: .5em;
  opacity: 1;
  left: 0;
  transform: rotate(0deg);
  transition: .25s ease-in-out;
}

#nav-icon span:nth-child(1) {
  top: 0;
}

#nav-icon span:nth-child(2) {
  top: .75em;
}

#nav-icon span:nth-child(3) {
  top: 1.5em;
}

#nav-icon.open span:nth-child(1) {
  top: .75em;
  transform: rotate(135deg);
}

#nav-icon.open span:nth-child(2) {
  opacity: 0;
  left: -4em;
}

#nav-icon.open span:nth-child(3) {
  top: .75em;
  transform: rotate(-135deg);
}

#nav-menu {
  z-index: 998;
  position: fixed;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  justify-content: space-around;
  align-content: center;
  background: var(--blue-color);
  transform: translateX(100%);
  transition: 0.3s ease-in-out;
  font-family: 'Salmela';
}

#nav-menu.open {
  transform: unset;
}

#nav-toggle {
  align-self: flex-end;
  height: 3em;
  padding: 1em;
}

.nav-link {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-content: center;
  justify-content: center;
  text-align: center;
}

.nav-link a {
  text-decoration: none;
  font-size: 2em;
  color: var(--white-color);
}
.nav-link a:clicked {
  color: inherit;
}
</style>



