<template>
<section id="home">
  <div id="home-background"></div>
  <div id="home-text">
    <div id="title">
      <div class="title-name">
        <h1>Mike Knowles</h1>
      </div>
      <div class="title-and">
        <h1>&</h1>
      </div>
      <div class="title-name">
        <h1>Chi Huynh</h1>
      </div>
    </div><!-- #title -->
    <div class="title-description">are getting married</div>
    <div class="title-text">Saturday, July 7, 2018</div>
    <div id="down-arrow">
      <a href="#story">♥</a>
    </div>
  </div>
</section>
</template>

<script>
export default {
  name: 'TheHome',
  data () {
    return {
      msg: 'What'
    }
  }
}
</script>

<style scoped>
#home {
  background-image: url('../../assets/images/wedding_invite_bw_crop.jpg');
  background-repeat: no-repeat;
  background-position: center;
  justify-content: flex-end;
}

#nav-toggle {
  align-self: flex-end;
  margin: 1em 1em 0 0;
  flex: 0;
}

#title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1em;
  margin-bottom: -1em;
  color: var(--gold-color);
}
.title-name {
  flex: 1;
  text-align: center;
}
.title-and {
  flex: 1;
  text-align: center;
  align-self: center;
  font-size: 1.5em;
}
.title-description {
  text-align: center;
  font-family: 'Merriweather';
  font-weight: bold;
  margin-bottom: 1em;
}
.title-text {
  text-align: center;
  font-family: 'Merriweather';
  font-weight: bold;
  font-size: 1.3em;
  color: var(--gold-color);
  margin-bottom: .75em;
}

#down-arrow {
  align-self: center;
  animation: arrow 3s infinite;
}

#down-arrow a {
  font-size: 2em;
  color: var(--blue-color);
  text-decoration: none;
}

@keyframes arrow {
  0% {transform: translateY(0);}
  40% {transform: translateY(15%);}
  80% {transform: translateY(0);}
  100% {transform: translateY(0);}
}

@media (min-width: 500px) {
  #home {
    background: var(--white-color);
    justify-content: center;
    flex-direction: row;
  }
  #home-background {
    background-image: url('../../assets/images/wedding_invite_bw.jpg');
  }
  #home-text {

  }

  #title {
    flex-direction: column;
  }
}
</style>



